@font-face {
    font-family: 'JetBrains Mono';
    src: url('JetBrainsMono-Medium.woff2') format('woff2'),
        url('JetBrainsMono-Medium.woff') format('woff'),
        url('JetBrainsMono-Medium.ttf') format('truetype'),
        url('JetBrainsMono-Medium.svg#JetBrainsMono-Medium') format('svg');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

